.home-page {
  .text-zone {
    position: absolute;
    left: 10%;
    top: 50%;
    transform: translateY(-50%);
    width: 40%;
    max-height: 90%;
  }
  h1 {
    color: #fff;
    font-size: 53px;
    margin: 0;
    font-family: 'Coolvetica';
    font-weight: 400;

    &::before {
      content: '<h1>';
      font-family: 'La Belle Aurore';
      color: #ccccff;
      font-size: 18px;
      position: absolute;
      margin-top: -40px;
      opacity: 0.6;
    }

    &::after {
      content: '<h1/>';
      font-family: 'La Belle Aurore';
      color: #ccccff;
      font-size: 18px;
      position: absolute;
      margin-top: 18px;
      margin-left: 20px;
      animation: fadeIn 1s 1.7s backwards;
    }
    img {
      width: 32px;
      margin-left: 20px;
      opacity: 0;
      height: auto;
      animation: rotateIn 1s linear both;
      animation-delay: 1.4s;
    }
  }
}

h2 {
  color: #8d8d8d;
  margin-top: 20px;
  font-weight: 400;
  font-size: 11px;
  font-family: sans-serif;
  letter-spacing: 3px;
  animation: fadeIn 1s 1.8s backwards;
}

.flat-button {
  color: #ccccff;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 4px;
  font-family: sans-serif;
  text-decoration: none;
  padding: 10px 18px;
  border: 1px solid #ccccff;
  margin-top: 25px;
  float: left;
  animation: fadeIn 1s 1.8s backwards;
  white-space: nowrap;

  &:hover {
    background: #ccccff;
    color: #333;
  }
}

.profile-picture {
  // height: 80%;
  height: 95%;
  width: auto;
  position: absolute;
  border-radius: 10;
  top: 10%;
  right: 8%;
  transform: translate(0%, -50%);
  animation: backInLeft 1s 1.2s;
  animation-fill-mode: forwards;
}

.profile-pic-container {
  height: 85%;
  width: auto;
  position: absolute;
  border-radius: 10;
  top: 5%;
  right: 8%;

  img {
    transform: translate(0%, -50%);
    animation: backInLeft 1s 1.2s;
    animation-fill-mode: forwards;
  }

  h2 {
    opacity: 0;
    animation: rotateInUpRight 1s linear both;
    animation-delay: 3s;
    // animation: zoomInLeft 1s;
    // animation-delay: 3s;
    // animation-fill-mode: forwards;
  }
}
